import imgIntro from "../../img/kom/kom_intro.jpg";
import imgMain from "../../img/kom/kom_main.jpg";
import kom_thumbnail from "../../img/kom/kom_thumbnail.jpg";
import kom_how from "../../img/kom/kom_how.jpg";
